.projectbox{
    margin: .5rem;
    height: 50vh;
    width: auto;
    border: 2px solid var(--black-body);
}
.projectbox img{
    height: 90%;
    width: 100%;
    padding: 0px;
    border-top-left-radius: .5rem;
    border-top-right-radius: .5rem;
}
 .projectbox .details{
    border-left: 2px solid var(--aqua);
    border-right: 2px solid var(--aqua);
    border-bottom: 2px solid var(--aqua);
    border-bottom-left-radius: .5rem;
    border-bottom-right-radius: .5rem;
    padding-bottom: 10px ;
    padding-top: 10px;
    display: flex;
}

.projectbox a i{
    color: var(--aqua);
    font-weight: bold;
    font-size: 1.5rem;
    font-weight: bold;
    padding-right: 20px;
    padding-left: 20px;
}
.projectbox p{
    color: var(--white);
    font-size: 1rem;
    font-weight: bold;
    margin:auto;
    padding-left: 12px;
}

@media only screen and (max-width: 1100px) {

    .projectbox img{
        height: 90%;
        width: 100%;
    }
}

@media only screen and (max-width: 830px) {

    .projectbox img{
        height: 90%;
        width: 100%;
        /* padding: 0 10px 0 10px; */
    }
}